import { useState, useCallback, useRef } from 'react'

export interface UseClipboardOptions {
  /**
   * 
   * @default 1500
   */
  copiedDuring?: number
  
  /**
   *  document.execCommand 
   * @default true
   */
  legacy?: boolean
  
  /**
   * 
   * @default 'clipboard'
   */
  source?: 'clipboard' | 'selection'
}

export interface UseClipboardReturn {
  /**
   * 
   */
  text: string
  
  /**
   *  API
   */
  isSupported: boolean
  
  /**
   *  copiedDuring 
   */
  copied: boolean
  
  /**
   * 
   */
  copy: (text: string) => Promise<void>
  
  /**
   * 
   */
  read: () => Promise<void>
}

/**
 *  API
 * 
 * @param options - 
 * @returns 
 * 
 * @example
 * ```tsx
 * function App() {
 *   const { text, copy, copied, isSupported } = useClipboard()
 *   const [input, setInput] = useState('')
 * 
 *   if (!isSupported) {
 *     return <div> API </div>
 *   }
 * 
 *   return (
 *     <div>
 *       <input 
 *         value={input} 
 *         onChange={(e) => setInput(e.target.value)}
 *         placeholder=""
 *       />
 *       <button onClick={() => copy(input)}>
 *         {copied ? '!' : ''}
 *       </button>
 *       <p>: {text}</p>
 *     </div>
 *   )
 * }
 * ```
 * 
 * @example
 * ```tsx
 * // 
 * function App() {
 *   const { copy, copied } = useClipboard({
 *     copiedDuring: 3000, // 3
 *     legacy: false // 
 *   })
 * 
 *   return (
 *     <button onClick={() => copy('Hello World')}>
 *       {copied ? '!' : ''}
 *     </button>
 *   )
 * }
 * ```
 */
export function useClipboard(options: UseClipboardOptions = {}): UseClipboardReturn {
  const {
    copiedDuring = 1500,
    legacy = true,
    source = 'clipboard'
  } = options

  const [text, setText] = useState('')
  const [copied, setCopied] = useState(false)
  const timeoutRef = useRef<NodeJS.Timeout>()

  //  API
  const isSupported = typeof navigator !== 'undefined' && 'clipboard' in navigator

  // 
  const copy = useCallback(async (value: string) => {
    if (!value) return

    try {
      //  API
      if (isSupported) {
        await navigator.clipboard.writeText(value)
      } else if (legacy) {
        // 
        await copyTextLegacy(value)
      } else {
        throw new Error('Clipboard API not supported and legacy method disabled')
      }

      setText(value)
      setCopied(true)

      // 
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current)
      }

      // 
      timeoutRef.current = setTimeout(() => {
        setCopied(false)
      }, copiedDuring)
    } catch (error) {
      console.error('Failed to copy text to clipboard:', error)
      throw error
    }
  }, [isSupported, legacy, copiedDuring])

  // 
  const read = useCallback(async () => {
    if (!isSupported) {
      console.warn('Clipboard API not supported')
      return
    }

    try {
      let clipboardText = ''
      
      if (source === 'clipboard') {
        clipboardText = await navigator.clipboard.readText()
      } else if (source === 'selection') {
        // 
        const selection = window.getSelection()
        clipboardText = selection ? selection.toString() : ''
      }
      
      setText(clipboardText)
    } catch (error) {
      console.error('Failed to read clipboard:', error)
      throw error
    }
  }, [isSupported, source])

  return {
    text,
    isSupported,
    copied,
    copy,
    read
  }
}

/**
 *  document.execCommand
 */
function copyTextLegacy(text: string): Promise<void> {
  return new Promise((resolve, reject) => {
    const textarea = document.createElement('textarea')
    textarea.value = text
    textarea.style.position = 'fixed'
    textarea.style.left = '-999999px'
    textarea.style.top = '-999999px'
    document.body.appendChild(textarea)
    
    try {
      textarea.focus()
      textarea.select()
      
      const successful = document.execCommand('copy')
      if (successful) {
        resolve()
      } else {
        reject(new Error('execCommand copy failed'))
      }
    } catch (error) {
      reject(error)
    } finally {
      document.body.removeChild(textarea)
    }
  })
}

/**
 *  API
 * 
 * @returns  API
 */
export function isClipboardSupported(): boolean {
  return typeof navigator !== 'undefined' && 'clipboard' in navigator
}

/**
 * 
 * 
 * @returns 
 */
export function isClipboardReadSupported(): boolean {
  return isClipboardSupported() && 'readText' in navigator.clipboard
}

/**
 * 
 * 
 * @returns 
 */
export function isClipboardWriteSupported(): boolean {
  return isClipboardSupported() && 'writeText' in navigator.clipboard
}