import { useState, useEffect, useCallback, useRef } from 'react';
import { isClient, DEFAULT_SERIALIZERS } from '@reactuse/shared';
import type { Serializer, StorageOptions } from '@reactuse/shared';

export interface UseSessionStorageOptions<T> extends Omit<StorageOptions<T>, 'listenToStorageChanges'> {
  /**
   * 
   * @default true
   */
  syncAcrossTabs?: boolean;
}

/**
 *  sessionStorage hook
 * 
 * @param key - 
 * @param defaultValue - 
 * @param options - 
 * @returns [value, setValue, removeValue]
 */
export function useSessionStorage<T = string>(
  key: string,
  defaultValue: T,
  options: UseSessionStorageOptions<T> = {}
): [T, (value: T | ((prev: T) => T)) => void, () => void] {
  const {
    serializer = DEFAULT_SERIALIZERS.any as Serializer<T>,
    syncAcrossTabs = true,
    onError = console.error,
  } = options;

  // 
  const getStoredValue = useCallback((): T => {
    if (!isClient) return defaultValue;
    
    try {
      const item = sessionStorage.getItem(key);
      if (item === null) return defaultValue;
      return serializer.read(item);
    } catch (error) {
      onError(error);
      return defaultValue;
    }
  }, [key, defaultValue, serializer, onError]);

  const [storedValue, setStoredValue] = useState<T>(getStoredValue);
  const isUpdatingRef = useRef(false);

  //  sessionStorage
  const setValue = useCallback((value: T | ((prev: T) => T)) => {
    if (!isClient) return;
    
    try {
      isUpdatingRef.current = true;
      const valueToStore = value instanceof Function ? value(storedValue) : value;
      setStoredValue(valueToStore);
      
      if (valueToStore === undefined) {
        sessionStorage.removeItem(key);
      } else {
        sessionStorage.setItem(key, serializer.write(valueToStore));
      }
    } catch (error) {
      onError(error);
    } finally {
      isUpdatingRef.current = false;
    }
  }, [key, storedValue, serializer, onError]);

  // 
  const removeValue = useCallback(() => {
    if (!isClient) return;
    
    try {
      isUpdatingRef.current = true;
      sessionStorage.removeItem(key);
      setStoredValue(defaultValue);
    } catch (error) {
      onError(error);
    } finally {
      isUpdatingRef.current = false;
    }
  }, [key, defaultValue, onError]);

  // 
  useEffect(() => {
    if (!isClient || !syncAcrossTabs) return;

    const handleStorageChange = (e: StorageEvent) => {
      if (e.key === key && e.storageArea === sessionStorage && !isUpdatingRef.current) {
        try {
          if (e.newValue === null) {
            setStoredValue(defaultValue);
          } else {
            setStoredValue(serializer.read(e.newValue));
          }
        } catch (error) {
          onError(error);
        }
      }
    };

    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, [key, defaultValue, serializer, syncAcrossTabs, onError]);

  // 
  useEffect(() => {
    const currentValue = getStoredValue();
    if (currentValue !== storedValue) {
      setStoredValue(currentValue);
    }
  }, [getStoredValue, storedValue]);

  return [storedValue, setValue, removeValue];
}

// 
export function useSessionStorageString(
  key: string,
  defaultValue: string = '',
  options?: Omit<UseSessionStorageOptions<string>, 'serializer'>
) {
  return useSessionStorage(key, defaultValue, {
    ...options,
    serializer: DEFAULT_SERIALIZERS.string,
  });
}

export function useSessionStorageNumber(
  key: string,
  defaultValue: number = 0,
  options?: Omit<UseSessionStorageOptions<number>, 'serializer'>
) {
  return useSessionStorage(key, defaultValue, {
    ...options,
    serializer: {
      read: (v: string) => {
        const num = Number(v);
        return Number.isNaN(num) ? defaultValue : num;
      },
      write: (v: number) => String(v),
    },
  });
}

export function useSessionStorageBoolean(
  key: string,
  defaultValue: boolean = false,
  options?: Omit<UseSessionStorageOptions<boolean>, 'serializer'>
) {
  return useSessionStorage(key, defaultValue, {
    ...options,
    serializer: DEFAULT_SERIALIZERS.boolean,
  });
}

export function useSessionStorageObject<T extends Record<string, any>>(
  key: string,
  defaultValue: T,
  options?: Omit<UseSessionStorageOptions<T>, 'serializer'>
) {
  return useSessionStorage(key, defaultValue, {
    ...options,
    serializer: DEFAULT_SERIALIZERS.object,
  });
}