import { useState, useEffect, useRef } from 'react';

export interface UseDebounceOptions {
  /**  300 */
  delay?: number;
  /**  false */
  leading?: boolean;
  /**  true */
  trailing?: boolean;
  /**  */
  maxWait?: number;
}

/**
 *  hook
 * 
 * @param value - 
 * @param delay -  300
 * @returns 
 */
export function useDebounce<T>(value: T, delay: number = 300): T {
  const [debouncedValue, setDebouncedValue] = useState<T>(value);

  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    return () => {
      clearTimeout(timer);
    };
  }, [value, delay]);

  return debouncedValue;
}

/**
 *  hook
 * 
 * @param fn - 
 * @param options - 
 * @returns 
 */
export function useDebounceFn<T extends (...args: any[]) => any>(
  fn: T,
  options: UseDebounceOptions = {}
): {
  /**  */
  run: T;
  /**  */
  cancel: () => void;
  /**  */
  flush: () => void;
} {
  const {
    delay = 300,
    leading = false,
    trailing = true,
    maxWait
  } = options;

  const fnRef = useRef(fn);
  const timerRef = useRef<NodeJS.Timeout | null>(null);
  const maxTimerRef = useRef<NodeJS.Timeout | null>(null);
  const lastCallTimeRef = useRef<number>(0);
  const lastInvokeTimeRef = useRef<number>(0);
  const argsRef = useRef<Parameters<T>>();
  const resultRef = useRef<ReturnType<T>>();

  // 
  fnRef.current = fn;

  const invokeFunc = (time: number) => {
    const args = argsRef.current!;
    argsRef.current = undefined;
    lastInvokeTimeRef.current = time;
    resultRef.current = fnRef.current(...args);
    return resultRef.current;
  };

  const leadingEdge = (time: number) => {
    lastInvokeTimeRef.current = time;
    timerRef.current = setTimeout(timerExpired, delay);
    return leading ? invokeFunc(time) : resultRef.current;
  };

  const remainingWait = (time: number) => {
    const timeSinceLastCall = time - lastCallTimeRef.current;
    const timeSinceLastInvoke = time - lastInvokeTimeRef.current;
    const timeWaiting = delay - timeSinceLastCall;

    return maxWait !== undefined
      ? Math.min(timeWaiting, maxWait - timeSinceLastInvoke)
      : timeWaiting;
  };

  const shouldInvoke = (time: number) => {
    const timeSinceLastCall = time - lastCallTimeRef.current;
    const timeSinceLastInvoke = time - lastInvokeTimeRef.current;

    return (
      lastCallTimeRef.current === 0 ||
      timeSinceLastCall >= delay ||
      timeSinceLastCall < 0 ||
      (maxWait !== undefined && timeSinceLastInvoke >= maxWait)
    );
  };

  const timerExpired = () => {
    const time = Date.now();
    if (shouldInvoke(time)) {
      return trailingEdge(time);
    }
    timerRef.current = setTimeout(timerExpired, remainingWait(time));
    return resultRef.current;
  };

  const trailingEdge = (time: number) => {
    timerRef.current = null;

    if (trailing && argsRef.current) {
      return invokeFunc(time);
    }
    argsRef.current = undefined;
    return resultRef.current;
  };

  const cancel = () => {
    if (timerRef.current !== null) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    if (maxTimerRef.current !== null) {
      clearTimeout(maxTimerRef.current);
      maxTimerRef.current = null;
    }
    lastInvokeTimeRef.current = 0;
    lastCallTimeRef.current = 0;
    argsRef.current = undefined;
  };

  const flush = () => {
    return timerRef.current === null ? resultRef.current : trailingEdge(Date.now());
  };

  const debounced = ((...args: Parameters<T>) => {
    const time = Date.now();
    const isInvoking = shouldInvoke(time);

    lastCallTimeRef.current = time;
    argsRef.current = args;

    if (isInvoking) {
      if (timerRef.current === null) {
        return leadingEdge(lastCallTimeRef.current);
      }
      if (maxWait !== undefined) {
        timerRef.current = setTimeout(timerExpired, delay);
        return invokeFunc(lastCallTimeRef.current);
      }
    }
    if (timerRef.current === null) {
      timerRef.current = setTimeout(timerExpired, delay);
    }
    return resultRef.current;
  }) as T;

  // 
  useEffect(() => {
    return () => {
      cancel();
    };
  }, []);

  return {
    run: debounced,
    cancel,
    flush
  };
}