import { useState, useCallback } from 'react';

export interface UseToggleReturn {
  /**  */
  value: boolean;
  /**  */
  toggle: () => void;
  /**  true */
  setTrue: () => void;
  /**  false */
  setFalse: () => void;
  /**  */
  setValue: (value: boolean | ((prev: boolean) => boolean)) => void;
}

/**
 *  hook
 * 
 * @param initialValue -  false
 * @returns 
 */
export function useToggle(initialValue: boolean = false): UseToggleReturn {
  const [value, setValue] = useState<boolean>(initialValue);

  const toggle = useCallback(() => {
    setValue(prev => !prev);
  }, []);

  const setTrue = useCallback(() => {
    setValue(true);
  }, []);

  const setFalse = useCallback(() => {
    setValue(false);
  }, []);

  const setValueCallback = useCallback((newValue: boolean | ((prev: boolean) => boolean)) => {
    setValue(newValue);
  }, []);

  return {
    value,
    toggle,
    setTrue,
    setFalse,
    setValue: setValueCallback,
  };
}

/**
 *  hook
 * 
 * @param values - 
 * @param initialIndex -  0
 * @returns 
 */
export function useToggleValues<T>(
  values: readonly T[],
  initialIndex: number = 0
): {
  value: T;
  index: number;
  next: () => void;
  prev: () => void;
  set: (index: number) => void;
  setValue: (value: T) => void;
} {
  const [index, setIndex] = useState<number>(
    Math.max(0, Math.min(initialIndex, values.length - 1))
  );

  const next = useCallback(() => {
    setIndex(prev => (prev + 1) % values.length);
  }, [values.length]);

  const prev = useCallback(() => {
    setIndex(prev => (prev - 1 + values.length) % values.length);
  }, [values.length]);

  const set = useCallback((newIndex: number) => {
    if (newIndex >= 0 && newIndex < values.length) {
      setIndex(newIndex);
    }
  }, [values.length]);

  const setValue = useCallback((value: T) => {
    const newIndex = values.indexOf(value);
    if (newIndex !== -1) {
      setIndex(newIndex);
    }
  }, [values]);

  return {
    value: values[index]!,
    index,
    next,
    prev,
    set,
    setValue,
  };
}